html {
  box-sizing: border-box;
  background: #ffc600;
  font-size: 10px;
}

*, :before, :after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: Amatic SC, cursive;
}

h1 {
  text-align: center;
  margin-bottom: 0;
  font-size: 10rem;
  line-height: 1;
}

.score {
  background: rgba(255, 255, 255, .2);
  border-radius: 1rem;
  padding: 0 3rem;
  line-height: 1;
}

.game {
  width: 600px;
  height: 400px;
  flex-wrap: wrap;
  margin: 0 auto;
  display: flex;
}

.hole {
  flex: 1 0 33.33%;
  position: relative;
  overflow: hidden;
}

.hole:after {
  content: "";
  width: 100%;
  height: 70px;
  z-index: 2;
  background: url("dirt.d4b9c9b0.svg") bottom / contain no-repeat;
  display: block;
  position: absolute;
  bottom: -30px;
}

.mole {
  width: 100%;
  height: 100%;
  background: url("mole.4bdb86cf.svg") bottom / 60% no-repeat;
  transition: all .4s;
  position: absolute;
  top: 100%;
}

.hole.up .mole {
  top: 0;
}

